  display: inline-block;
  width: 35px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  background: rgba(11,80,107,0.3);
  border: 1px solid rgb(11,80,107);
  color: rgb(13,108,131);
  margin-right: 10px;
  font-size: 12px;
}
span.btn:nth-child(2){
  border-color: rgb(21,90,104);
  background: rgba(21,90,104,0.3);
  color: rgb(27,159,158);
}
span.btn:nth-child(3){
  border-color: rgb(16,91,92);
  background: rgba(16,91,92,0.3);
  color: rgb(23,177,136);
}
.right .top{
  position: relative;
  width: 100%;
  .el{
    display: inline-block;
    >div{
      margin: 0 10px;
    }
  }
  .dao{
    position: absolute;
    right: 10px;
    top: 20px;
    width: 85px;
    height: 30px;
    color: #fff;
    background: #5296D1;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    border-radius: 10px;
  }
}
