span.btn {
  display: inline-block;
  width: 35px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  background: rgba(11, 80, 107, 0.3);
  border: 1px solid #0b506b;
  color: #0d6c83;
  margin-right: 10px;
  font-size: 12px;
}
span.btn:nth-child(2) {
  border-color: #155a68;
  background: rgba(21, 90, 104, 0.3);
  color: #1b9f9e;
}
span.btn:nth-child(3) {
  border-color: #105b5c;
  background: rgba(16, 91, 92, 0.3);
  color: #17b188;
}
.right .top {
  position: relative;
  width: 100%;
}
.right .top .el {
  display: inline-block;
}
.right .top .el > div {
  margin: 0 10px;
}
.right .top .dao {
  position: absolute;
  right: 10px;
  top: 20px;
  width: 85px;
  height: 30px;
  color: #fff;
  background: #5296D1;
  text-align: center;
  line-height: 30px;
  font-size: 14px;
  border-radius: 10px;
}
